(function () {

  // dayjsduration 
  dayjs.extend(dayjs_plugin_duration);

  let baseTime = '6/15/2022',
    format = 'M/DD/YYYY HH:mm:ss',
    diffTime = dayjs().diff(dayjs(new Date(baseTime), format), 'second'),
    duration = dayjs.duration(diffTime, 'second'); //  dayjs  duration 

  // let durationday = dayjs.duration(diff, 'day');


  setInterval(() => {
    diffTime = dayjs().diff(dayjs(new Date(baseTime), format), 'second');
    duration = dayjs.duration(diffTime, 'second');

    let years = Math.floor(duration.asYears()); // 
    let days = Math.floor(duration.asDays() % 365); // 

    $('.footer_custom_text').text(`
    
    ${years || 0} 
    ${days || 0} 
    ${duration.hours() || 0} 
    ${duration.minutes() || 0} 
    ${duration.seconds() || 0} 
    `);

  }, 1000);

})();